<template>
    <div>
        <!--  -->
        <el-card>
            <!--  -->
            <el-row type="flex">
                <el-col :span="6">
                    <el-input v-model="query" placeholder="">
                        <el-button slot="append" icon="el-icon-search" @click="requestDrugs"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="6"></el-col>
                <el-col :span="6">
                    <el-button type="primary" @click="addFormVisible = true" style="font-size: 18px">
                        <i class="el-icon-circle-plus-outline" style="font-size: 22px;"></i>
                        </el-button>
                </el-col>
            </el-row>
            <!--  -->
            <el-table :data="drugData" stripe style="width: 100%" border>
                <el-table-column label="" prop="drId"></el-table-column>
                <el-table-column label="" prop="drName"></el-table-column>
                <el-table-column label="" prop="drNumber"></el-table-column>
                <el-table-column label="" prop="drUnit"></el-table-column>
                <el-table-column label="" prop="drPrice"></el-table-column>
                <el-table-column label="" prop="drPublisher"></el-table-column>
                <el-table-column label="" width="200" fixed="right">
                    <template slot-scope="scope">
                        <el-button style="font-size: 14px" type="success" @click="modifyDialog(scope.row.drId)"><i
                                class="el-icon-edit-outline" style="font-size: 22px;"></i></el-button>
                        <el-button style="font-size: 14px" type="danger" @click="deleteDialog(scope.row.drId)"><i
                                class="el-icon-delete" style="font-size: 22px;"></i></el-button>
                    </template>
                </el-table-column>
            </el-table>

            <!--  -->
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" background
                layout="total, sizes, prev, pager, next, jumper" :current-page="pageNumber" :page-size="size"
                :page-sizes="[1, 2, 4, 8, 16]" :total="total">
            </el-pagination>
        </el-card>

        <!--  -->
        <el-dialog title="" :visible.sync="addFormVisible">
            <el-form :model="addForm" :rules="rules" ref="ruleForm">
                <el-form-item label="" prop="drId" label-width="80px">
                    <el-input v-model.number="addForm.drId"></el-input>
                </el-form-item>
                <el-form-item label="" prop="drName" label-width="80px">
                    <el-input v-model="addForm.drName"></el-input>
                </el-form-item>
                <el-form-item label="" prop="drNumber" label-width="80px">
                    <el-input-number v-model="addForm.drNumber" :min="0" :max="1000"></el-input-number>
                </el-form-item>
                <el-form-item label="" prop="drUnit" label-width="80px">
                    <el-radio v-model="addForm.drUnit" label=""></el-radio>
                    <el-radio v-model="addForm.drUnit" label=""></el-radio>
                    <el-radio v-model="addForm.drUnit" label=""></el-radio>
                </el-form-item>
                <el-form-item label="" prop="drPrice" label-width="80px">
                    <el-input v-model="addForm.drPrice" @input="formatPrice('addForm')" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="drPublisher" label-width="80px">
                    <el-input v-model="addForm.drPublisher"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addFormVisible = false" style="font-size: 18px;"><i class="el-icon-close"
                        style="font-size: 20px;"></i>  </el-button>
                <el-button type="primary" @click="addDrug('ruleForm')" style="font-size: 18px;"><i class="el-icon-check"
                        style="font-size: 20px;"></i>  </el-button>
            </div>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="modifyFormVisible">
            <el-form :model="modifyForm" :rules="rules" ref="ruleForm">
                <el-form-item label="" prop="drId" label-width="80px">
                    <el-input v-model.number="modifyForm.drId" disabled></el-input>
                </el-form-item>
                <el-form-item label="" prop="drName" label-width="80px">
                    <el-input v-model="modifyForm.drName"></el-input>
                </el-form-item>
                <el-form-item label="" prop="drNumber" label-width="80px">
                    <el-input-number v-model="modifyForm.drNumber" :min="0" :max="1000"></el-input-number>
                </el-form-item>
                <el-form-item label="" prop="drUnit" label-width="80px">
                    <el-radio v-model="modifyForm.drUnit" label=""></el-radio>
                    <el-radio v-model="modifyForm.drUnit" label=""></el-radio>
                    <el-radio v-model="modifyForm.drUnit" label=""></el-radio>
                </el-form-item>
                <el-form-item label="" prop="drPrice" label-width="80px">
                    <el-input v-model="modifyForm.drPrice" @input="formatPrice('modifyForm')"
                        placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="drPublisher" label-width="80px">
                    <el-input v-model="modifyForm.drPublisher"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="modifyFormVisible = false" style="font-size: 18px;"><i class="el-icon-close"
                        style="font-size: 20px;"></i>  </el-button>
                <el-button type="primary" @click="modifyDrug('ruleForm')" style="font-size: 18px;"><i
                        class="el-icon-check" style="font-size: 20px;"></i>  </el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
import request from "@/utils/request.js";
export default {
    name: "DrugList",
    data() {
        return {
            pageNumber: 1,
            size: 8,
            query: "",
            drugData: [],
            total: 3,
            addFormVisible: false,
            addForm: {},
            rules: {
                drId: [
                    { required: true, message: "", trigger: "blur" },
                    {
                        type: "number",
                        message: "",
                        trigger: "blur",
                    },
                ],
                drName: [
                    { required: true, message: "", trigger: "blur" },
                    {
                        min: 1,
                        max: 50,
                        message: "150",
                        trigger: "blur",
                    },
                ],
                drUnit: [
                    { required: true, message: "", trigger: "blur" },
                ],
                drPrice: [
                    { required: true, message: "", trigger: "blur" },
                ],
                drPublisher: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur",
                    },
                    {
                        min: 1,
                        max: 50,
                        message: "150",
                        trigger: "blur",
                    },
                ],
            },
            modifyFormVisible: false,
            modifyForm: {},
        };
    },
    methods: {
        //
        modifyDrug(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    request
                        .post("admin/modifyDrug", {
                                drId: this.modifyForm.drId,
                                drName: this.modifyForm.drName,
                                drNumber: this.modifyForm.drNumber,
                                drPrice: this.modifyForm.drPrice,
                                drUnit: this.modifyForm.drUnit,
                                drPublisher: this.modifyForm.drPublisher,
                        })
                        .then((res) => {
                            if (res.data.status !== 200)
                                return this.$message.error("");
                            this.modifyFormVisible = false;
                            this.requestDrugs();
                            this.$message.success("");
                            console.log(res);
                        });
                } else {
                    console.log("error submit!!");
                    return false;
                }
            });
        },
        //
        modifyDialog(id) {
            request
                .post("admin/findDrug", {
                        drId: id
                })
                .then((res) => {
                    if (res.data.status !== 200)
                        return this.$message.error("");
                    this.modifyForm = res.data.data;
                    this.modifyFormVisible = true;
                    console.log(res.data.data);
                });
        },
        // 
        formatPrice(formType) {
            let value = this[formType].drPrice;
            if (!value) return;
            // 
            value = value.replace(/[^\d.]/g, "");

            // 
            value = value.replace(/^(\d*\.)(.*)\./, "$1$2");
            // 
            if (value.includes(".")) {
                let parts = value.split(".");
                parts[1] = parts[1].slice(0, 2); // 
                value = parts.join(".");
            }
            //  `.`  0
            if (value.startsWith(".")) {
                value = "0" + value;
            }
            //  0 `0001`  `1`
            value = value.replace(/^0+(\d)/, "$1");
            // 
            this[formType].drPrice = value;
        },
        //
        deleteDrug(id) {
            request
                .post("admin/deleteDrug", {
                        drId: id
                })
                .then((res) => {
                    this.requestDrugs();
                    console.log(res);
                });
        },
        //
        deleteDialog(id) {
            this.$confirm(", ?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning",
            })
                .then(() => {
                    this.deleteDrug(id);
                    this.$message({
                        type: "success",
                        message: "!",
                    });
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "",
                    });
                });
        },
        //
        addDrug(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    request
                        .post("admin/addDrug", {
                            drId: this.addForm.drId,
                            drName: this.addForm.drName,
                            drNumber: this.addForm.drNumber,
                            drPrice: this.addForm.drPrice,
                            drUnit: this.addForm.drUnit,
                            drPublisher: this.addForm.drPublisher,
                        })
                        .then((res) => {
                            if (res.data.status !== 200)
                                return this.$message.error(res.data.message);
                            this.addFormVisible = false;
                            this.requestDrugs();
                            this.$message.success(res.data.message);
                            console.log(res);
                        });
                } else {
                    console.log("error submit!!");
                    return false;
                }
            });
        },
        //
        handleSizeChange(size) {
            this.size = size;
            this.requestDrugs();
        },
        //   
        handleCurrentChange(num) {
            console.log(num);
            this.pageNumber = num;
            this.requestDrugs();
        },
        // 
        requestDrugs() {
            request
                .get("admin/findAllDrugs", {
                    params: {
                        pageNumber: this.pageNumber,
                        size: this.size,
                        query: this.query,
                    },
                })
                .then((res) => {
                    this.drugData = res.data.data.drugs;
                    this.total = res.data.data.total;
                    console.log(res.data.data);
                });
        },
    },
    created() {
        this.requestDrugs();
    },
};
</script>
<style scoped lang="scss">
.el-table {
    margin-top: 20px;
    margin-bottom: 20px;
}

.el-form {
    margin-top: 0;
}
</style>