<template>
    <!--  -->
    <el-card>
        <!--  -->
        <el-row type="flex">
            <el-col :span="8">
                <el-input v-model="query" placeholder="">
                    <el-button slot="append" icon="el-icon-search" @click="requestDoctors"></el-button>
                </el-input>
            </el-col>
            <el-col :span="4"></el-col>
            <el-col :span="4">
                <el-button type="primary" @click="addFormVisible = true" style="font-size: 18px;">
                    <i class="el-icon-circle-plus-outline" style="font-size: 22px;"></i>
                    </el-button>
            </el-col>
            <el-col :span="4"></el-col>
            <!--  -->
            <el-col :span="2">
                <el-upload class="upload-demo" action="doctor/uploadExcel" accept=".xlsx,.xls " :limit="1"
                    :show-file-list="false" :on-progress="handleProgress" :on-exceed="handleExceed"
                    :on-success="handleSuccess" :on-error="handleError" :file-list="fileList">
                    <el-button size="small" type="success"></el-button>
                </el-upload>
            </el-col>

            <el-col :span="2">
                <el-button size="small" type="success" @click="exportDoctors"></el-button></el-col>
        </el-row>
        <!--  -->
        <el-table :data="doctorData" stripe style="width: 100%" border>
            <el-table-column prop="dId" label="" width="100">
            </el-table-column>
            <el-table-column prop="dName" label="" width="80">
            </el-table-column>
            <el-table-column prop="details.dGender" label="" width="60">
            </el-table-column>
            <el-table-column prop="details.dPost" label="" width="100">
            </el-table-column>
            <el-table-column prop="details.dSection" label="" width="100">
            </el-table-column>
            <el-table-column prop="details.dCard" label="">
            </el-table-column>
            <el-table-column prop="details.dPhone" label="">
            </el-table-column>
            <el-table-column prop="details.dEmail" label="" width="170">
            </el-table-column>
            <el-table-column prop="details.dAvgStar" label="/5" width="80">
            </el-table-column>
            <el-table-column prop="details.dPrice" label="/" width="100">
            </el-table-column>
            <el-table-column prop="dState" label="" width="80">
                <template slot-scope="scope">
                    <el-tag type="success" v-if="scope.row.dState === 1"></el-tag>
                    <el-tag type="danger" v-else></el-tag>
                </template>
            </el-table-column>
            <el-table-column label="" width="200" fixed="right">
                <template slot-scope="scope">
                    <el-button style="font-size: 14px;" type="success" @click="modifyDialog(scope.row.dId)"><i
                            class="el-icon-edit-outline" style="font-size: 22px;"></i></el-button>
                    <el-button style="font-size: 14px;" type="danger" @click="deleteDialog(scope.row.dId)"><i
                            class="el-icon-delete" style="font-size: 22px;"></i></el-button>
                </template>
            </el-table-column>
        </el-table>

        <!--  -->
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" background
            layout="total, sizes, prev, pager, next, jumper" :current-page="pageNumber" :page-size="size"
            :page-sizes="[1, 2, 4, 8, 16]" :total="total">
        </el-pagination>
        <!--  -->
        <el-dialog title="" :visible.sync="addFormVisible">
            <el-form :model="addForm" :rules="rules" ref="ruleForm">
                <el-form-item label="" label-width="80px" prop="dId">
                    <el-input v-model.number="addForm.dId" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" label-width="80px">
                    <el-input v-model="addForm.dPassword" autocomplete="off" disabled></el-input>
                </el-form-item>
                <el-form-item label="" label-width="80px" prop="dName">
                    <el-input v-model="addForm.dName" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" label-width="80px">
                    <el-radio v-model="addForm.dGender" label=""></el-radio>
                    <el-radio v-model="addForm.dGender" label=""></el-radio>
                </el-form-item>
                <el-form-item label="" label-width="80px" prop="dPost">
                    <el-select v-model="addForm.dPost" placeholder="">
                        <el-option v-for="post in posts" :key="post" :label="post" :value="post">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" label-width="80px" prop="dSection">
                    <el-select v-model="addForm.dSection" filterable placeholder="">
                        <el-option v-for="section in sections" :key="section" :label="section" :value="section">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" label-width="80px" prop="dCard">
                    <el-input v-model="addForm.dCard" autocomplete="off" maxlength="18"></el-input>
                </el-form-item>
                <el-form-item label="" label-width="80px" prop="dPhone">
                    <el-input v-model="addForm.dPhone" autocomplete="off" maxlength="11"></el-input>
                </el-form-item>
                <el-form-item label="" label-width="80px" prop="dEmail">
                    <el-input v-model="addForm.dEmail" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" label-width="80px" prop="dPrice">
                    <el-input v-model="addForm.dPrice" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" label-width="80px" prop="dIntroduction">
                    <el-input type="textarea" :rows="5" placeholder="" v-model="addForm.dIntroduction">
                    </el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addFormVisible = false" style="font-size: 18px;"><i class="el-icon-close"
                        style="font-size: 20px;"></i>  </el-button>
                <el-button type="primary" @click="addDoctor('ruleForm')" style="font-size: 18px;"><i
                        class="el-icon-check" style="font-size: 20px;"></i>  </el-button>
            </div>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="modifyFormVisible">
            <el-form :model="modifyForm" :rules="rules" ref="ruleForm">
                <el-form-item label="" label-width="80px" prop="dId">
                    <el-input v-model.number="modifyForm.dId" autocomplete="off" disabled></el-input>
                </el-form-item>

                <el-form-item label="" label-width="80px" prop="dName">
                    <el-input v-model="modifyForm.dName" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" label-width="80px">
                    <el-radio v-model="modifyForm.dGender" label=""></el-radio>
                    <el-radio v-model="modifyForm.dGender" label=""></el-radio>
                </el-form-item>
                <el-form-item label="" label-width="80px" prop="dPost">
                    <el-select v-model="modifyForm.dPost" placeholder="">
                        <el-option v-for="post in posts" :key="post" :label="post" :value="post">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" label-width="80px" prop="dSection">
                    <el-select v-model="modifyForm.dSection" filterable placeholder="">
                        <el-option v-for="section in sections" :key="section" :label="section" :value="section">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" label-width="80px" prop="dCard">
                    <el-input v-model="modifyForm.dCard" autocomplete="off" maxlength="18"></el-input>
                </el-form-item>
                <el-form-item label="" label-width="80px" prop="dPhone">
                    <el-input v-model="modifyForm.dPhone" autocomplete="off" maxlength="11"></el-input>
                </el-form-item>
                <el-form-item label="" label-width="80px" prop="dEmail">
                    <el-input v-model="modifyForm.dEmail" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" label-width="80px" prop="dPrice">
                    <el-input v-model="modifyForm.dPrice" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" label-width="80px" prop="dIntroduction">
                    <el-input type="textarea" :rows="5" placeholder="" v-model="modifyForm.dIntroduction">
                    </el-input>
                </el-form-item>
                <el-form-item label="" label-width="80px" prop="dState">
                    <el-select v-model="modifyForm.dState" placeholder="">
                        <el-option label="" :value="1"></el-option>
                        <el-option label="" :value="0"></el-option>
                    </el-select>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="modifyFormVisible = false" style="font-size: 18px;"><i class="el-icon-close"
                        style="font-size: 20px;"></i>  </el-button>
                <el-button type="primary" @click="modifyDoctor('ruleForm')" style="font-size: 18px;"><i
                        class="el-icon-check" style="font-size: 20px;"></i>  </el-button>
            </div>
        </el-dialog>
    </el-card>
</template>
<script>
import request from "@/utils/request.js";
export default {
    name: "DoctorList",
    data() {
        var validateMoblie = (rule, value, callback) => {
            if (value === undefined) {
                callback(new Error(""));
            } else {
                let reg =
                    /^1(3[0-9]|4[5,7]|5[0,1,2,3,5,6,7,8,9]|6[2,5,6,7]|7[0,1,7,8]|8[0-9]|9[1,3,8,9])\d{8}$/;
                if (!reg.test(value)) {
                    callback(new Error(""));
                }
                callback();
            }
        };
        var validateCard = (rule, value, callback) => {
            if (value === undefined) {
                callback(new Error(""));
            } else {
                let reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
                if (!reg.test(value)) {
                    callback(new Error(""));
                }
                callback();
            }
        };
        return {
            //
            fileList: [],
            pageNumber: 1,
            size: 8,
            query: "",
            doctorData: [],
            total: 3,
            addFormVisible: false,
            addForm: {
                dPassword: 123456,
                dGender: "",
            },
            posts: ["", "", ""],
            sections: [
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
                "",
            ],
            rules: {
                dId: [
                    { required: true, message: "", trigger: "blur" },
                    {
                        type: "number",
                        message: "",
                        trigger: "blur",
                    },
                ],
                dName: [
                    { required: true, message: "", trigger: "blur" },
                    {
                        min: 2,
                        max: 5,
                        message: "25",
                        trigger: "blur",
                    },
                ],
                dPost: [
                    { required: true, message: "", trigger: "blur" },
                ],
                dSection: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur",
                    },
                ],
                dEmail: [
                    { required: true, message: "", trigger: "blur" },
                    {
                        type: "email",
                        message: "",
                        trigger: ["blur", "change"],
                    },
                ],
                dPrice: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur",
                    },
                ],
                dPhone: [{ validator: validateMoblie }],
                dCard: [{ validator: validateCard }],
                dIntroduction: [
                    {
                        required: true,
                        message: "",
                        trigger: "blur",
                    },
                ],
            },
            modifyFormVisible: false,
            modifyForm: {},
        };
    },
    methods: {
        //
        exportDoctors() {
            window.location.href = "http://localhost:9999/doctor/downloadExcel";
        },
        //
        handleProgress() {
            this.$message.warning("");
        },
        // 
        handleSuccess() {
            this.$message.success("");
            this.requestDoctors();
        },
        handleError() {
            //this.$message.error("");
            this.$message.success("");
            this.requestDoctors();
        },
        handleExceed() {
            this.$message.warning(" 1 ");
        },
        //
        modifyDoctor(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    request
                        .post("admin/modifyDoctor", {
                            dId: this.modifyForm.dId,
                            dGender: this.modifyForm.dGender,
                            dName: this.modifyForm.dName,
                            dPost: this.modifyForm.dPost,
                            dSection: this.modifyForm.dSection,
                            dPhone: this.modifyForm.dPhone,
                            dEmail: this.modifyForm.dEmail,
                            dCard: this.modifyForm.dCard,
                            dPrice: this.modifyForm.dPrice,
                            dIntroduction: this.modifyForm.dIntroduction,
                            dState: this.modifyForm.dState
                        })
                        .then((res) => {
                            if (res.data.status !== 200) {
                                console.log(res.data.status);
                                return this.$message.error("");
                            }
                            this.modifyFormVisible = false;
                            this.requestDoctors();
                            this.$message.success(res.data.message);
                            console.log(res);
                        });
                } else {
                    console.log("error submit!!");
                    return false;
                }
            });
        },
        //
        modifyDialog(id) {
            console.log(id);
            request
                .post("admin/findDoctor", {
                    dId: id
                })
                .then((res) => {
                    if (res.data.status !== 200) {
                        this.$message.error("");
                        return;
                    }
                    this.modifyForm = res.data.data;
                    this.modifyFormVisible = true;
                    console.log(res);
                });
        },
        //
        deleteDoctor(id) {
            request
                .post("admin/deleteDoctor", {
                    dId: id,
                })
                .then((res) => {
                    this.requestDoctors();
                    console.log(res);
                });
        },
        deleteDialog(dId) {
            this.$confirm(", ?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning",
            })
                .then(() => {
                    request
                        .post("admin/deleteDoctor", { dId }) //  POST 
                        .then((res) => {
                            if (res.data.status !== 200) {
                                return this.$message.error("" + res.data.message);
                            }

                            this.$message.success("");
                            this.requestDoctors(); //  UI
                        })
                        .catch((error) => {
                            console.error(error);
                            this.$message.error("!");
                        });
                })
                .catch(() => {
                    this.$message.info("");
                });
        },

        // 
        addDoctor(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    request
                        .post("admin/addDoctor", {
                            dId: this.addForm.dId,
                            dGender: this.addForm.dGender,
                            dPassword: this.addForm.dPassword,
                            dName: this.addForm.dName,
                            dPost: this.addForm.dPost,
                            dSection: this.addForm.dSection,
                            dPhone: this.addForm.dPhone,
                            dEmail: this.addForm.dEmail,
                            dCard: this.addForm.dCard,
                            dPrice: this.addForm.dPrice,
                            dIntroduction: this.addForm.dIntroduction
                        })
                        .then((res) => {
                            // 200
                            if (res.data.status !== 200)
                                return this.$message.error(res.data.message);
                            this.addFormVisible = false;
                            this.requestDoctors();
                            this.$message.success(res.data.message);
                        })
                } else {
                    console.log("error submit!!");
                    return false;
                }
            });
        },

        //
        handleSizeChange(size) {
            this.size = size;
            this.requestDoctors();
        },
        //   
        handleCurrentChange(num) {
            console.log(num);
            this.pageNumber = num;
            this.requestDoctors();
        },
        // 
        requestDoctors() {
            request
                .get("admin/findAllDoctors", {
                    params: {
                        pageNumber: this.pageNumber,
                        size: this.size,
                        query: this.query,
                    }
                })
                .then((res) => {
                    this.doctorData = res.data.data.doctors;
                    this.total = res.data.data.total;
                    console.log(res.data.data);
                });
        },
    },
    // 
    created() {
        this.requestDoctors();
    },
};
</script>
<style scoped lang="scss">
.el-table {
    margin-top: 20px;
    margin-bottom: 20px;
}

.el-form {
    margin-top: 0;
}
</style>