<template>
  <div>
    <el-card>
      <el-row>
        <el-col :span="7">
          <el-input disabled v-model="oId" class="orderInput"></el-input>
        </el-col>
        <el-col :span="7">
          <el-input disabled v-model="pId" class="orderInput"></el-input></el-col>
        <el-col :span="7">
          <el-input disabled v-model="pName" class="orderInput"></el-input>
        </el-col>
        <el-col :span="3">
          <el-button type="success" style="font-size: 18px;" icon="iconfont icon-r-paper" @click="submitClick">
            </el-button>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="7">
          <el-input disabled v-model="pGender" class="orderInput"></el-input>
        </el-col>
        <el-col :span="7">
          <el-input disabled v-model="pPhone" class="orderInput"></el-input></el-col>
        <el-col :span="7">
          <el-input disabled v-model="dName" class="orderInput"></el-input>
        </el-col>
        <el-col :span="3">
          <el-button type="success" style="font-size: 18px;" @click="openAdvice">
            <i class="el-icon-circle-plus-outline" style="font-size: 20px;"></i>
            /</el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-row>
        <el-col :span="12">
          <el-input v-model="queryDrug" placeholder="" class="drugInput">
            <el-button slot="append" icon="el-icon-search" @click="requestDrug"></el-button>
          </el-input>
          <el-table :data="drugData" stripe border>
            <el-table-column label="" prop="drId"></el-table-column>
            <el-table-column label="" prop="drName"></el-table-column>
            <el-table-column label="" prop="drNumber"></el-table-column>
            <el-table-column label="" prop="drUnit"></el-table-column>
            <el-table-column label="" prop="drPrice"></el-table-column>
            <el-table-column label="" width="120" fixed="right">
              <template slot-scope="scope">
                <el-button type="success" style="font-size: 14px;" @click="addDrug(scope.row.drId)">
                  <i class="el-icon-circle-plus-outline" style="font-size: 16px;"></i>
                  </el-button>
              </template>
            </el-table-column>
          </el-table>
          <!--  -->
          <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" background
            layout="total, sizes, prev, pager, next" :total="total" :page-size="size" :page-sizes="[1, 2, 4, 8, 16]">

          </el-pagination>
          <el-row></el-row>
        </el-col>
        <!--  -->
        <el-col :span="12" class="drugRigth">
          <el-table stripe border :data="drugBuyData" class="rigthTable">
            <el-table-column label="" prop="drId"></el-table-column>
            <el-table-column label="" prop="drName"></el-table-column>
            <el-table-column label="" prop="drPrice"></el-table-column>
            <el-table-column label="" prop="drNum"></el-table-column>
            <el-table-column label="" prop="drSum"></el-table-column>
            <el-table-column label="" width="120" fixed="right">
              <template slot-scope="scope">
                <el-button type="danger" style="font-size: 14px;" icon="el-icon-delete"
                  @click="deleteDrug(scope.row.drId)"> </el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-tag>{{ drugTotalPrice.toFixed(2) }}</el-tag>

        </el-col>

      </el-row>
      <!--  -->
      <el-row>
        <el-col :span="12">
          <el-input v-model="queryCheck" placeholder="" class="drugInput">
            <el-button slot="append" icon="el-icon-search" @click="requestCheck"></el-button>
          </el-input>
          <el-table stripe border :data="checkData">
            <el-table-column label="" prop="chId"></el-table-column>
            <el-table-column label="" prop="chName"></el-table-column>
            <el-table-column label="" prop="chPrice"></el-table-column>
            <el-table-column label="" width="120" fixed="right">
              <template slot-scope="scope">
                <el-button type="success" style="font-size: 14px;" @click="addCheck(scope.row.chId)">
                  <i class="el-icon-circle-plus-outline" style="font-size: 16px;"></i>
                  </el-button>
              </template>
            </el-table-column>
          </el-table>
          <!--  -->
          <el-pagination @size-change="checkSizeChange" @current-change="checkCurrentChange" background
            layout="total, prev, pager, next" :total="checkTotal" :page-size="checkSize">
          </el-pagination>
        </el-col>
        <!--  -->
        <el-col :span="12" class="drugRigth">
          <el-table stripe border class="rigthTable" :data="checkBuyData">
            <el-table-column label="" prop="chId"></el-table-column>
            <el-table-column label="" prop="chName"></el-table-column>
            <el-table-column label="" prop="chPrice"></el-table-column>
            <el-table-column label="" width="120" fixed="right">
              <template slot-scope="scope">
                <el-button type="danger" style="font-size: 14px;" icon="el-icon-delete"
                  @click="deleteCheck(scope.row.chId)"> </el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-tag>{{ checkTotalPrice.toFixed(2) }}</el-tag>
        </el-col>
      </el-row>

    </el-card>


    <!--  -->
    <el-dialog title="" :visible.sync="adviceFormVisible">
      <el-input type="textarea" :rows="8" placeholder="" v-model="advice">
      </el-input>
      <div slot="footer" class="dialog-footer">
        <el-button @click="adviceFormVisible = false" style="font-size: 18px;"><i class="el-icon-close"
            style="font-size: 20px;"></i>  </el-button>
        <el-button type="primary" @click="holdAdvice"></el-button>
      </div>
    </el-dialog>

  </div>
</template>
<script>
import jwtDecode from "jwt-decode";
import { getToken } from "@/utils/storage.js";
import request from "@/utils/request.js";
export default {
  name: "dealOrderAgain",
  data() {
    return {
      oId: 1,
      pId: 1,
      dId: 0,
      pName: "aa",
      pGender: "as",
      pPhone: "asd",
      dName: "",
      drugData: [],
      size: 4,
      pageNumber: 1,
      total: 3,
      queryDrug: "",
      drugBuyData: [],
      drugTotalPrice: 0,
      //
      advice: "",
      //
      checkData: [],
      queryCheck: "",
      checkTotal: 3,
      checkSize: 4,
      checkPageNumber: 1,
      checkTotalPrice: 0,
      checkBuyData: [],
      // 
      adviceFormVisible: false,

    }
  },
  mounted() {
    this.getUserInfo();// 
  },
  methods: {
    getUserInfo() {
      const token = getToken(); //  token
      if (!token) {
        return this.$message.error("");
      }

      request.get("/getUserInfo", {
        headers: { Authorization: `Bearer ${token}` } //  token
      })
        .then(res => {
          if (res.data.status !== 200) {
            return this.$message.error("");
          }
          console.log(res.data);
          this.dName = res.data.data.userName; // 
        })
        .catch(err => {
          console.error(err);
          this.$message.error("");
        });
    },
    //id
    reduceDrugNumber(drId, usedNumber) {
      request.post("drug/reduceDrugNumber", {
        drId: drId,
        usedNumber: usedNumber,
      })
        .then(res => {
          if (res.data.status !== 200) {
            this.$message.error("");
          }
        })
    },
    //
    submitClick() {

      let data = {
        oId: this.dataPackage().oId,
        oAdvice: this.dataPackage().oAdvice,
        oDrug: this.dataPackage().oDrug,
        oCheck: this.dataPackage().oCheck,
        oTotalPrice: this.dataPackage().oTotalPrice
      }
      request.post("doctor/updateOrderByAdd", data)
        .then(res => {
          console.log(res)
          if (res.data.status !== 200) {
            this.$message.error("");
            return;
          }
          for (let i = 0; i < this.drugBuyData.length; i++) {
            this.reduceDrugNumber(this.drugBuyData[i].drId, this.drugBuyData[i].drNum);
          };
          this.$message.success("");
          this.$router.push("/doctorOrder");
          console.log(res.data)
        })
        .catch(err => {
          console.error(err);
        })

    },
    //
    dataPackage() {
      let oDrug = " // ";
      let oCheck = " // ";
      let oTotalPrice = 0;
      let oId = this.oId;
      let oAdvice = this.advice;
      for (let i = 0; i < this.drugBuyData.length; i++) {
        oDrug += this.drugBuyData[i].drName + "*" + this.drugBuyData[i].drPrice + "()*" + this.drugBuyData[i].drNum + " ";
      };
      for (let i = 0; i < this.checkBuyData.length; i++) {
        oCheck += this.checkBuyData[i].chName + "*" + this.checkBuyData[i].chPrice + "() ";
      }
      oCheck += "" + this.checkTotalPrice + "";
      oDrug += "" + this.drugTotalPrice.toFixed(2) + "";
      oTotalPrice = this.checkTotalPrice + this.drugTotalPrice;
      return { oId, oAdvice, oDrug, oCheck, oTotalPrice };
    },
    //
    holdAdvice() {
      this.adviceFormVisible = false;
      this.$message.success("");
    },
    //
    openAdvice() {
      this.adviceFormVisible = true;

    },
    //
    deleteCheck(chId) {
      for (let i = 0; i < this.checkBuyData.length; i++) {
        if (this.checkBuyData[i].chId === chId) {
          this.checkTotalPrice -= this.checkBuyData[i].chPrice;//
          this.checkBuyData.splice(i, 1);
        }
      }
    },
    //
    addCheck(chId) {
      request.post("admin/findCheck", {
        chId: chId
      })
        .then(res => {
          if (res.data.status != 200)
            this.$message.error("");
          //
          this.checkBuyData.push(
            {
              chId: res.data.data.chId,
              chPrice: res.data.data.chPrice,
              chName: res.data.data.chName,
            }
          )
          // 
          this.checkTotalPrice = Math.round((this.checkTotalPrice + res.data.data.chPrice) * 100) / 100;
          console.log(res.data.data);
        })

    },
    //
    checkSizeChange(size) {
      this.checkSize = size;
      this.requestCheck();

    },
    //
    checkCurrentChange(num) {
      this.checkPageNumber = num;
      this.requestCheck();
    },
    //
    requestCheck() {
      request.get("admin/findAllChecks", {
        params: {
          size: this.checkSize,
          pageNumber: this.checkPageNumber,
          query: this.queryCheck
        }
      })
        .then(res => {
          if (res.data.status != 200)
            this.$message.error("");
          this.checkData = res.data.data.checks;
          this.checkTotal = res.data.data.total;
          console.log(res);
        })
    },
    /**
     * 
     */
    //
    deleteDrug(drId) {
      for (let i = 0; i < this.drugBuyData.length; i++) {
        if (this.drugBuyData[i].drId === drId) {
          for (let j = 0; j < this.drugData.length; j++) {
            if (this.drugData[j].drId === drId)
              this.drugData[j].drNumber += 1;
          }
          this.drugBuyData[i].drNum -= 1;
          this.drugBuyData[i].drSum = Math.round(this.drugBuyData[i].drPrice * 100 * this.drugBuyData[i].drNum) / 100;
          this.drugTotalPrice -= this.drugBuyData[i].drPrice;//
          if (this.drugBuyData[i].drNum === 0)
            this.drugBuyData.splice(i, 1);
        }
      }
    },
    //
    addDrug(drId) {
      request.post("admin/findDrug", {
        drId: drId
      })
        .then(res => {
          if (res.data.status != 200)
            this.$message.error("");
          //
          for (let i = 0; i < this.drugBuyData.length; i++) {
            if (this.drugBuyData[i].drId === res.data.data.drId) {
              for (let j = 0; j < this.drugData.length; j++) {
                if (this.drugData[j].drId === res.data.data.drId && this.drugData[j].drNumber > 0) {
                  this.drugData[j].drNumber -= 1;
                  this.drugBuyData[i].drNum += 1;
                  this.drugBuyData[i].drSum = Math.round(this.drugBuyData[i].drPrice * this.drugBuyData[i].drNum * 100) / 100;
                  this.drugTotalPrice += this.drugBuyData[i].drPrice;//
                }
              }
              return;
            }
          }
          for (let j = 0; j < this.drugData.length; j++) {
            if (this.drugData[j].drId === res.data.data.drId && this.drugData[j].drNumber <= 0)
              return;
          }
          this.drugBuyData.push(
            {
              drId: res.data.data.drId,
              drPrice: res.data.data.drPrice,
              drName: res.data.data.drName,
              drNum: 1,
              drSum: res.data.data.drPrice,
            }
          )
          for (let j = 0; j < this.drugData.length; j++) {
            if (this.drugData[j].drId === res.data.data.drId)
              this.drugData[j].drNumber -= 1;
          }
          this.drugTotalPrice = Math.round((this.drugTotalPrice + res.data.data.drPrice) * 100) / 100;//
          console.log(res.data.data);
        })

    },
    //
    handleSizeChange(size) {
      this.size = size;
      this.requestDrug();

    },
    //
    handleCurrentChange(num) {
      this.pageNumber = num;
      this.requestDrug();
    },

    //
    requestDrug() {
      request.get("admin/findAllDrugs", {
        params: {
          size: this.size,
          pageNumber: this.pageNumber,
          query: this.queryDrug
        }
      })
        .then(res => {
          if (res.data.status != 200)
            this.$message.error("");
          this.drugData = res.data.data.drugs;
          this.total = res.data.data.total;


          console.log(res);
        })
    },
    //
    requestPatient() {
      request.get("patient/findPatientById", {
        params: {
          pId: this.pId
        }
      })
        .then(res => {
          console.log(res);
          if (res.data.status != 200)
            this.$message.error("");
          this.pName = res.data.data.pName;
          this.pGender = res.data.data.pGender;
          this.pPhone = res.data.data.pPhone;

        })

    },
    //token
    tokenDecode(token) {
      if (token !== null)
        return jwtDecode(token);
    },
  },
  created() {
    this.dId = this.tokenDecode(getToken()).dId;
    this.oId = this.$route.query.oId;
    this.pId = this.$route.query.pId;
    this.requestPatient();
    this.requestDrug();
    this.requestCheck();
  },
};
</script>
<style lang="scss">
// 
.drugRigth {
  text-align: center;
}

.el-tag {
  margin: 8px;
}

.rigthTable {
  margin-top: 56px;
  margin-left: 8px;
}

.drugInput {
  margin-top: 8px;
  margin-bottom: 8px;
}

.el-row {
  margin: 5px;
}

.orderInput {
  width: 240px;
}

.el-pagination {
  margin: 8px;
}
</style>