<template>
    <div>
        <!--  -->
        <el-card>
            <!--  -->
            <el-row type="flex">
                <el-col :span="6">
                    <el-input v-model="query" placeholder="">
                        <el-button slot="append" icon="el-icon-search" @click="requestChecks"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="6"></el-col>
                <el-col :span="6">
                    <el-button type="primary" style="font-size: 18px" @click="addFormVisible = true">
                        <i class="el-icon-circle-plus-outline" style="font-size: 22px;"></i>
                        </el-button>
                </el-col>
            </el-row>
            <!--  -->
            <el-table :data="checkData" stripe style="width: 100%" border>
                <el-table-column label="" prop="chId"></el-table-column>
                <el-table-column label="" prop="chName"></el-table-column>
                <el-table-column label="/" prop="chPrice"></el-table-column>
                <el-table-column label="" width="200" fixed="right">
                    <template slot-scope="scope">
                        <el-button style="font-size: 14px" type="success" @click="modifyDialog(scope.row.chId)"><i
                                class="el-icon-edit-outline" style="font-size: 22px;"></i></el-button>
                        <el-button style="font-size: 14px" type="danger" @click="deleteDialog(scope.row.chId)"><i
                                class="el-icon-delete" style="font-size: 22px;"></i></el-button>
                    </template>
                </el-table-column>
            </el-table>

            <!--  -->
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" background
                layout="total, sizes, prev, pager, next, jumper" :current-page="pageNumber" :page-size="size"
                :page-sizes="[1, 2, 4, 8, 16]" :total="total">
            </el-pagination>
        </el-card>

        <!--  -->
        <el-dialog title="" :visible.sync="addFormVisible">
            <el-form :model="addForm" :rules="rules" ref="ruleForm">
                <el-form-item label="" prop="chId" label-width="80px">
                    <el-input v-model.number="addForm.chId"></el-input>
                </el-form-item>
                <el-form-item label="" prop="chName" label-width="80px">
                    <el-input v-model="addForm.chName"></el-input>
                </el-form-item>
                <el-form-item label="" prop="chPrice" label-width="80px">
                    <el-input v-model="addForm.chPrice"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addFormVisible = false" style="font-size: 18px;"><i class="el-icon-close"
                        style="font-size: 20px;"></i>  </el-button>
                <el-button type="primary" @click="addCheck('ruleForm')" style="font-size: 18px;"><i
                        class="el-icon-check" style="font-size: 20px;"></i>  </el-button>
            </div>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="modifyFormVisible">
            <el-form :model="modifyForm" :rules="rules" ref="ruleForm">
                <el-form-item label="" prop="chId" label-width="80px">
                    <el-input v-model.number="modifyForm.chId" disabled></el-input>
                </el-form-item>
                <el-form-item label="" prop="chName" label-width="80px">
                    <el-input v-model="modifyForm.chName"></el-input>
                </el-form-item>
                <el-form-item label="" prop="chPrice" label-width="80px">
                    <el-input v-model="modifyForm.chPrice"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="modifyFormVisible = false" style="font-size: 18px;"><i class="el-icon-close"
                        style="font-size: 20px;"></i>  </el-button>
                <el-button type="primary" @click="modifyCheck('ruleForm')" style="font-size: 18px;"><i
                        class="el-icon-check" style="font-size: 20px;"></i>  </el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
import request from "@/utils/request.js";
export default {
    name: "CheckList",
    data() {
        return {
            pageNumber: 1,
            size: 8,
            query: "",
            checkData: [],
            total: 3,
            addFormVisible: false,
            addForm: {},
            rules: {
                chId: [
                    { required: true, message: "", trigger: "blur" },
                    {
                        type: "number",
                        message: "",
                        trigger: "blur",
                    },
                ],
                chName: [
                    { required: true, message: "", trigger: "blur" },
                    {
                        min: 1,
                        max: 50,
                        message: "150",
                        trigger: "blur",
                    },
                ],
                chPrice: [
                    { required: true, message: "", trigger: "blur" },
                ],
            },
            modifyFormVisible: false,
            modifyForm: {},
        };
    },
    methods: {
        //
        modifyCheck(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    request
                        .post("admin/modifyCheck", {
                            chId: this.modifyForm.chId,
                            chName: this.modifyForm.chName,
                            chPrice: this.modifyForm.chPrice,
                        })
                        .then((res) => {
                            if (res.data.status !== 200)
                                return this.$message.error("");
                            this.modifyFormVisible = false;
                            this.requestChecks();
                            this.$message.success("");
                            console.log(res);
                        });
                } else {
                    console.log("error submit!!");
                    return false;
                }
            });
        },
        //
        modifyDialog(id) {
            request
                .post("admin/findCheck", {
                    chId: id,
                })
                .then((res) => {
                    if (res.data.status !== 200)
                        return this.$message.error("");
                    this.modifyForm = res.data.data;
                    this.modifyFormVisible = true;
                    console.log(res);
                });
        },
        //
        deleteCheck(id) {
            request
                .post("admin/deleteCheck", {
                    chId: id
                })
                .then((res) => {
                    this.requestChecks();
                    console.log(res);
                });
        },
        //
        deleteDialog(id) {
            this.$confirm(", ?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning",
            })
                .then(() => {
                    this.deleteCheck(id);
                    this.$message({
                        type: "success",
                        message: "!",
                    });
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "",
                    });
                });
        },
        //
        addCheck(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    request
                        .post("admin/addCheck", {
                            chId: this.addForm.chId,
                            chName: this.addForm.chName,
                            chPrice: this.addForm.chPrice,
                        })
                        .then((res) => {
                            if (res.data.status !== 200)
                                return this.$message.error(
                                    ""
                                );
                            this.addFormVisible = false;
                            this.requestChecks();
                            this.$message.success("");
                            console.log(res);
                        });
                } else {
                    console.log("error submit!!");
                    return false;
                }
            });
        },
        //
        handleSizeChange(size) {
            this.size = size;
            this.requestChecks();
        },
        //   
        handleCurrentChange(num) {
            console.log(num);
            this.pageNumber = num;
            this.requestChecks();
        },
        // 
        requestChecks() {
            request
                .get("admin/findAllChecks", {
                    params: {
                        pageNumber: this.pageNumber,
                        size: this.size,
                        query: this.query
                    }
                })
                .then((res) => {
                    this.checkData = res.data.data.checks;
                    this.total = res.data.data.total;
                    console.log(res.data.data);
                });
        },
    },
    created() {
        this.requestChecks();
    },
};
</script>
<style scoped lang="scss">
.el-table {
    margin-top: 20px;
    margin-bottom: 20px;
}

.el-form {
    margin-top: 0;
}
</style>