<template>
    <div>
        <el-card>
            <el-table :data="bedData" border stripe>
                <el-table-column label="" prop="bId" v-model="bedData.bId"></el-table-column>
                <el-table-column label="id" prop="pId" v-model="bedData.pId"></el-table-column>
                <el-table-column label="id" prop="dId" v-model="bedData.dId"></el-table-column>
                <el-table-column label="" prop="bReason" v-model="bedData.bReason"></el-table-column>
                <el-table-column label="" prop="bStart" v-model="bedData.bStart"></el-table-column>
                <el-table-column label="" prop="bEnd" v-model="bedData.bEnd"></el-table-column>
            </el-table>
        </el-card>
    </div>
</template>
<script>
import jwtDecode from "jwt-decode";
import request from "@/utils/request.js";
import { getToken } from "@/utils/storage.js";
export default {
    name: "MyBed",
    data() {
        return {
            bedData:[],
            userId:1,
        }
    },
    methods: {
        //
        requestBed(){
            request.get("patient/findBedByPid", {
                params: {
                    pId: this.userId
                }
            })
            .then(res => {
                if(res.data.status !== 200)
                    return this.$message.error("");
                console.log(res.data.data);
                this.bedData = res.data.data;
            })

        },
    },
    created(){
        //token
        const token = getToken();
        this.userId = jwtDecode(token).user_id;
        this.requestBed();
    }
}
</script>