<template>
    <!--  -->
    <el-card>
        <!--  -->
        <el-row type="flex">
            <el-col :span="6">
                <el-input v-model="query" placeholder="id">
                    <el-button slot="append" icon="el-icon-search" @click="requestOrders"></el-button>
                </el-input>
            </el-col>
        </el-row>
        <!--  -->
        <el-table :data="orderData" stripe style="width: 100%" border>
            <el-table-column prop="oId" label="" width="80px" fixed="left"></el-table-column>
            <el-table-column prop="pId" label="id" width="80px"></el-table-column>

            <el-table-column prop="dId" label="id" width="100px">
            </el-table-column>

            <el-table-column prop="oStart" label="" width="180px"></el-table-column>
            <el-table-column prop="oEnd" label="" width="180px"></el-table-column>
            <el-table-column prop="oRecord" label="" width="400px"></el-table-column>
            <el-table-column prop="oDrug" label="" width="180px"></el-table-column>
            <el-table-column prop="oCheck" label="" width="180px"></el-table-column>
            <el-table-column prop="oTotalPrice" label="/" width="80px"></el-table-column>
            <el-table-column prop="oPriceState" label="" width="100px">
                <template slot-scope="scope">
                    <el-tag type="success" v-if="scope.row.oPriceState === 1"></el-tag>
                    <el-tag type="danger" v-if="scope.row.oPriceState === 0 && scope.row.oState === 1"></el-tag>
                </template>
            </el-table-column>
            <el-table-column label="" prop="oGhAlipay" width="200px">
                <template slot-scope="scope">
                    <el-tag type="success" v-if="
                        scope.row.oGhAlipay === 'PAID'
                    "></el-tag>
                    <el-tag type="danger" v-if="
                        scope.row.oGhAlipay === null
                    "></el-tag>
                </template>
            </el-table-column>
            <el-table-column prop="oState" label="" width="100px">
                <template slot-scope="scope">
                    <el-tag type="success" v-if="scope.row.oState === 1 && scope.row.oPriceState === 1"></el-tag>
                    <el-tag type="danger" v-if="scope.row.oState === 0 && scope.row.oState === 0"></el-tag>
                </template>
            </el-table-column>
            <el-table-column label="" width="140" fixed="right">
                <template slot-scope="scope">
                    <el-button icon="el-icon-delete" style="font-size: 14px" type="danger"
                        @click="deleteDialog(scope.row.oId)"></el-button>
                </template>
            </el-table-column>
        </el-table>

        <!--  -->
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" background
            layout="total, sizes, prev, pager, next, jumper" :current-page="pageNumber" :page-size="size"
            :page-sizes="[1, 2, 4, 8, 16]" :total="total">
        </el-pagination>
    </el-card>
</template>
<script>
import request from "@/utils/request.js";
export default {
    name: "OrderList",
    data() {
        return {
            pageNumber: 1,
            size: 8,
            query: "",
            orderData: [],
            total: 3,
        };
    },
    methods: {
        //
        deleteOrder(id) {
            request
                .get("admin/deleteOrder", {
                    params: {
                        oId: id,
                    },
                })
                .then((res) => {
                    this.requestOrders();
                    console.log(res);
                });
        },
        //
        deleteDialog(id) {
            this.$confirm(", ?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning",
            })
                .then(() => {
                    this.deleteOrder(id);
                    this.$message({
                        type: "success",
                        message: "!",
                    });
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: "",
                    });
                });
        },
        //
        handleSizeChange(size) {
            this.size = size;
            this.requestOrders();
        },
        //   
        handleCurrentChange(num) {
            console.log(num);
            this.pageNumber = num;
            this.requestOrders();
        },
        // 
        requestOrders() {
            request
                .get("admin/findAllOrders", {
                    params: {
                        pageNumber: this.pageNumber,
                        size: this.size,
                        query: this.query,
                    },
                })
                .then((res) => {
                    this.orderData = res.data.data.orders;
                    this.total = res.data.data.total;
                    console.log(res.data.data);
                });
        },
    },
    created() {
        this.requestOrders();
    },
};
</script>
<style scoped lang="scss">
.el-table {
    margin-top: 20px;
    margin-bottom: 20px;
}

.el-form {
    margin-top: 0;
}
</style>