<template>
    <div>
        <!--  -->
        <el-card>
            <!--  -->
            <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{ path: '/orderOperate' }"></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>

            <!--  -->
            <div class="head">
                <div>
                    <i class="iconfont icon-r-user1" style="margin: 5px; font-size: 26px">{{ sectionOpt }}</i>
                </div>

                <!--  -->
                <div>
                    <i class="iconfont icon-r-paper" style="font-size: 22px"></i>
                    <ul class="dateUl">
                        <li v-for="monthDay in monthDays" :key="monthDay">
                            <el-button icon="iconfont icon-r-paper" @click="dateClick(monthDay)">{{
                                monthDay }}</el-button>
                        </li>
                    </ul>
                </div>
            </div>

            <!--  -->
            <el-table :data="sectionData" stripe style="width: 100%" border>
                <el-table-column type="index" label="" width="60"></el-table-column>
                <el-table-column prop="dId" label="" width="80">
                </el-table-column>
                <el-table-column prop="dName" label="" width="80">
                </el-table-column>
                <el-table-column prop="dGender" label="" width="60">
                </el-table-column>
                <el-table-column prop="dPost" label="" width="100">
                </el-table-column>
                <el-table-column prop="dSection" label="" width="100"></el-table-column>
                <el-table-column prop="dIntroduction" label="">
                </el-table-column>
                <el-table-column prop="dPrice" label="/" width="80">
                </el-table-column>
                <el-table-column prop="dAvgStar" label="/5" width="80">
                </el-table-column>
                <el-table-column label="" width="140" v-if="clickTag">
                    <template slot-scope="scope">
                        <el-button class="iconfont icon-r-paper" style="font-size: 14px" type="warning"
                            @click="openClick(scope.row.dId, scope.row.dName, scope.row.dPrice)">
                            </el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
        <!--  -->
        <el-dialog title="" :visible.sync="orderFormVisible">
            <el-form :model="orderForm" ref="orderForm" :rules="orderRules">
                <el-form-item label="" label-width="100px" prop="oTime">
                    <el-select v-model="orderForm.oTime" placeholder="" no-data-text="">
                        <el-option v-for="time in times" :key="time" :label="time" :value="time">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" label-width="100px">
                    <el-input v-model="orderForm.orderDate" autocomplete="off" disabled></el-input>
                </el-form-item>
                <el-form-item label="" label-width="100px">
                    <el-input v-model="orderForm.dId" autocomplete="off" disabled></el-input>
                </el-form-item>
                <el-form-item label="" label-width="100px">
                    <el-input v-model="orderForm.dName" autocomplete="off" disabled></el-input>
                </el-form-item>
                <el-form-item label="" label-width="100px">
                    <el-input v-model="orderForm.pName" autocomplete="off" disabled></el-input>
                </el-form-item>
                <el-form-item label="" label-width="100px">
                    <el-input v-model="orderForm.pCard" autocomplete="off" disabled></el-input>
                </el-form-item>
                <el-form-item label="" label-width="100px">
                    <el-input v-model="orderForm.dPrice" autocomplete="off" disabled style="width: 150px;"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="orderFormVisible = false" style="font-size: 18px;"><i class="el-icon-close"
                        style="font-size: 20px;"></i>  </el-button>
                <el-button type="primary" @click="orderSuccess('orderForm')" style="font-size: 18px;"><i
                        class="el-icon-check" style="font-size: 20px;"></i>  </el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
import jwtDecode from "jwt-decode";
import request from "@/utils/request.js";
import { getToken } from "@/utils/storage.js";
export default {
    name: "sectionMessage",
    data() {
        return {
            sectionOpt: this.$route.query.sectionOpt,
            sectionData: [],
            monthDays: [],
            clickTag: false,
            orderFormVisible: false,
            orderForm: { orderDate: "" },
            times: [],
            orderRules: {
                oTime: [
                    { required: true, message: "", trigger: "blur" },
                ],
            },
            //
            orderDate: "",
            //oId
            idTime: "",
        };
    },
    methods: {
        //,
        requestTime(id) {
            this.idTime = id + this.orderDate;
            request
                .get("patient/findOrderTime", {
                    params: {
                        arId: this.idTime,
                    },
                })
                .then((res) => {
                    const date = new Date(this.orderDate);
                    const today = new Date();
                    const isToday =
                        date.getFullYear() === today.getFullYear() &&
                        date.getMonth() === today.getMonth() &&
                        date.getDate() === today.getDate();
                    var array = [];
                    if (!this.isTimeAfterTarget("09:30") || !isToday) {
                        array.push(
                            "00:01-09:30  " + "    " + res.data.data.eTOn
                        );
                    }
                    if (!this.isTimeAfterTarget("10:30") || !isToday) {
                        array.push(
                            "09:30-10:30  " + "    " + res.data.data.nTOt
                        );
                    }
                    if (!this.isTimeAfterTarget("11:30") || !isToday) {
                        array.push(
                            "10:30-11:30  " + "    " + res.data.data.tTOe
                        );
                    }
                    if (!this.isTimeAfterTarget("15:30") || !isToday) {
                        array.push(
                            "14:30-15:30  " + "    " + res.data.data.fTOf
                        );
                    }
                    if (!this.isTimeAfterTarget("16:30") || !isToday) {
                        array.push(
                            "15:30-16:30  " + "    " + res.data.data.fTOs
                        );
                    }
                    if (!this.isTimeAfterTarget("23:59") || !isToday) {
                        array.push(
                            "16:30-23:59  " + "    " + res.data.data.sTOs
                        );
                    }
                    this.times = array;
                });
        },
        isTimeAfterTarget(timeString) {
            // timeString(09:30)
            const currentTime = new Date();

            // 
            const [targetHour, targetMinute] = timeString.split(":");

            // 
            const targetTime = new Date();
            targetTime.setHours(targetHour);
            targetTime.setMinutes(targetMinute);
            targetTime.setSeconds(0);

            // 
            return currentTime > targetTime;
        },
        // 
        orderSuccess(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    // 
                    request
                        .post("patient/addOrder", {
                            pId: this.userId,
                            dId: this.orderForm.dId,
                            oStart:
                                this.orderForm.orderDate +
                                " " +
                                this.orderForm.oTime,
                            arId: this.idTime,
                        })
                        .then((res) => {
                            console.log(res.data);
                            if (res.data.status !== 200)
                                return this.$message.error(
                                    ""
                                );

                            // 
                            const orderId = res.data.oId;

                            // 
                            return request.post("alipay/pay", {
                                subject: this.orderForm.pName + "",
                                oId: "gh" + orderId,
                                totalAmount: this.orderForm.dPrice, // 
                                passbackParams: "registration"  // 
                            });
                        })
                        .then(data => {
                            console.log(data);

                            if (data.data.payUrl) {
                                // 
                                window.open(data.data.payUrl, '_blank', 'width=800,height=600');
                                this.oId = data.data.tradeNo;
                                console.log(" oId:", this.oId);
                                // 
                                const pollInterval = 5000; // 5

                                const pollOrderStatus = setInterval(() => {
                                    request
                                        .get("order/o_state", {
                                            params: {
                                                oId: this.oId.substring(2) // 
                                            },
                                        })
                                        .then((res) => {
                                            if (res.data.message === "PAID") {
                                                clearInterval(pollOrderStatus); // 
                                                this.orderFormVisible = false;
                                                this.$message.success("");
                                                this.orderForm.oTime = '';
                                                this.$router.push("myOrder");
                                            }
                                        })
                                        .catch((error) => {
                                            console.error(":", error);
                                        });
                                }, pollInterval);
                            } else {
                                console.error(":", data);
                            }
                        })
                        .catch(error => {
                            console.error(":", error);
                        });
                } else {
                    console.log("error submit!!");
                    return false;
                }
            });
        },
        //
        openClick(id, name, price) {
            this.orderForm.dId = id;
            this.orderForm.dName = name;
            this.orderForm.pName = this.patientData.pName;
            this.orderForm.pCard = this.patientData.pCard;
            this.orderForm.dPrice = price;
            this.orderFormVisible = true;
            //
            this.requestTime(id);
        },
        //
        dateClick(date) {
            //
            const nowDate = new Date();
            let year = nowDate.getFullYear();
            this.orderForm.orderDate = year + "-" + date;
            let dateYear = year + "-" + date;
            this.orderDate = dateYear;
            request
                .get("/patient/findByTime", {
                    params: {
                        arTime: dateYear,
                        dSection: this.sectionOpt,
                    },
                })
                .then((res) => {
                    this.sectionData = res.data.data;
                    console.log(res.data.data);

                    this.clickTag = true;
                });
        },
        //7
        nowDay(num) {
            var nowDate = new Date();
            var currentHour = nowDate.getHours();
            var currentMinute = nowDate.getMinutes();

            // 23:59
            if (
                currentHour > 23 ||
                (currentHour === 23 && currentMinute > 59)
            ) {
                num++; // 
            }

            nowDate.setDate(nowDate.getDate() + num);
            var month = nowDate.getMonth() + 1;
            var date = nowDate.getDate();
            if (date < 10) {
                date = "0" + date;
            }
            if (month < 10) {
                month = "0" + month;
            }
            var time = month + "-" + date;
            this.monthDays.push(time);
        },
        //
        requestSection() {
            request
                .get("patient/findDoctorBySection", {
                    params: {
                        dSection: this.$route.query.sectionOpt,
                    },
                })
                .then((res) => {
                    if (res.data.status !== 200)
                        return this.$message.error("");
                    this.sectionData = res.data.data.doctors;
                    console.log(res.data.data.doctors);
                });
        },
        //
        requestPatient() {
            request.get("patient/findPatientById", {
                params: {
                    pId: this.userId
                }
            })
                .then(res => {
                    if (res.data.status != 200)
                        return this.$message.error("");
                    this.patientData = res.data.data;
                    console.log(this.patientData);
                })
        }
    },
    created() {
        //7
        for (var i = 0; i < 7; i++) {
            this.nowDay(i);
        }
        //
        this.requestSection();
        //token
        const token = getToken();
        this.userId = jwtDecode(token).user_id;
        this.requestPatient();
    },
};
</script>
<style scoped lang="scss">
.dateUl li {
    display: inline;
    padding: 1px;
}

.dateUl {
    margin: 10px;
}

.el-breadcrumb {
    margin: 8px;
}

.head {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.el-form {
    margin-top: 0;
}
</style>