<template>
    <div>
        <el-card>
            <el-table :data="orderData" stripe style="width: 100%" border>
                <el-table-column prop="oId" label="" width="80px"></el-table-column>
                <el-table-column prop="pId" label="id" width="75px"></el-table-column>
                <el-table-column prop="pName" label="" width="75px"></el-table-column>
                <el-table-column prop="dId" label="id" width="75px"></el-table-column>
                <el-table-column prop="dName" label="" width="80px"></el-table-column>

                <el-table-column prop="oStart" label="" width="195px"></el-table-column>
                <el-table-column prop="oEnd" label="" width="185px"></el-table-column>
                <el-table-column prop="oTotalPrice" label="/" width="100px"></el-table-column>
                <el-table-column prop="oPriceState" label="" width="150px">
                    <template slot-scope="scope">
                        <el-tag type="success" v-if="scope.row.oPriceState === 1"></el-tag>
                        <el-button type="warning" icon="iconfont icon-r-mark1" style="font-size: 14px" v-if="
                            scope.row.oPriceState === 0 &&
                            scope.row.oState === 1
                        " @click="priceClick(scope.row.oId, scope.row.dId, scope.row.oTotalPrice, scope.row.pName)">
                            </el-button>
                    </template>
                </el-table-column>
                <el-table-column prop="oState" label="" width="100px">
                    <template slot-scope="scope">
                        <el-tag type="success" v-if="
                            scope.row.oState === 1 &&
                            scope.row.oPriceState === 1
                        "></el-tag>
                        <el-tag type="danger" v-if="
                            scope.row.oState === 0
                        "></el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="success" icon="el-icon-search" style="font-size: 14px"
                            @click="seeReport(scope.row.oId)" v-if="
                                scope.row.oState === 1 &&
                                scope.row.oPriceState === 1
                            "> </el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
        <!--  -->
        <el-dialog title="" :visible.sync="starVisible">
            <div>
                <h3>
                    {{ dId }}&nbsp;{{ dName }}&nbsp;
                </h3>
            </div>
            <div>
                <el-rate v-model="star" show-text> </el-rate>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button @click="starVisible = false" style="font-size: 18px;"><i class="el-icon-close"
                        style="font-size: 20px;"></i>  </el-button>
                <el-button type="primary" @click="starClick" style="font-size: 18px;"><i class="el-icon-check"
                        style="font-size: 20px;"></i>  </el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
import request from "@/utils/request.js";
import jwtDecode from "jwt-decode";
import { getToken } from "@/utils/storage.js";
export default {
    name: "MyOrder",
    data() {
        return {
            userId: 1,
            orderData: [],
            star: 5,
            starVisible: false,
            dId: 1,
            dName: "",
        };
    },
    methods: {
        //
        starClick() {
            request
                .get("doctor/updateStar", {
                    params: {
                        dId: this.dId,
                        dStar: this.star,
                    },
                })
                .then((res) => {
                    if (res.data.status !== 200)
                        return this.$message.error("");
                    this.$message.success(res.data.message);
                    this.starVisible = false;
                });
        },
        //
        seeReport(id) {
            window.location.href =
                "http://127.0.0.1:5000/patient/pdf?oId=" + id;
        },
        //
        priceClick(oId, dId, oTotalPrice, pName) {
            //  POST 
            fetch("http://127.0.0.1:5000/alipay/pay", {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({
                    subject: `${pName}`,
                    oId: oId,
                    totalAmount: oTotalPrice,
                    passbackParams: "order" //
                })
            })
                .then(response => response.json())
                .then(data => {
                    if (data.payUrl) {
                        // 
                        window.open(data.payUrl, '_blank', 'width=800,height=600');
                    } else {
                        console.error(":", data);
                    }
                })
                .catch(error => {
                    console.error(":", error);
                });


            // 
            const pollInterval = 5000; // 5
            const pollOrderStatus = setInterval(() => {
                request
                    .get("order/status", {
                        params: {
                            oId: oId,
                        },
                    })
                    .then((res) => {
                        if (res.data.message === "PAID") {
                            clearInterval(pollOrderStatus); // 
                            this.$message.success("");
                            // 
                            this.findDoctor(dId);
                        }
                    })
                    .catch((error) => {
                        console.error(":", error);
                    });
            }, pollInterval);
        },

        findDoctor(dId) {
            request
                .post("doctor/findDoctorById", {
                    dId: dId
                })
                .then((res) => {
                    if (res.data.status === 200) {
                        this.dId = res.data.data.dId;
                        this.dName = res.data.data.dName;
                        this.starVisible = true; // 
                        this.requestOrder();  // 
                    } else {
                        this.$message.error("");
                    }
                });
        },

        //
        requestOrder() {
            request
                .get("patient/findOrderByPid", {
                    params: {
                        pId: this.userId,
                    },
                })
                .then((res) => {
                    console.log(res.data.data);
                    if (res.data.status !== 200) {
                        this.$message.error("");
                    }
                    this.orderData = res.data.data;
                });
        },
    },
    created() {
        //token
        const token = getToken();
        this.userId = jwtDecode(token).user_id;
        this.userName = jwtDecode(token).user_name;
        this.requestOrder();
        console.log(this.userId)
    },
};
</script>
<style lang="scss" scoped>
.el-dialog div {
    text-align: center;
    margin-bottom: 8px;
}
</style>