<template>
  <el-container>
    <!--  -->
    <el-header>
      <div class="head-bar">
        <div class="header-ico">
          <img src="@/assets/img/1.png" style="width: 55px;
    height: 55px;
    margin-top: 7px;
    border-radius: 50%;">
        </div>
        <div class="logo" @click="reloadComponent"></div>
        <div class="time-display">
           {{ currentTime }}
        </div>
        <div class="head-right">
          <div class="head-user-con">
            <div class="user-avatar">
              <img src="../assets/img/logo.svg" />
            </div>

            <el-dropdown @command="handleCommand" class="user-name" trigger="click">
              <span class="el-dropdown-link">
                <span><b>{{ userName }}</b>&nbsp;&nbsp;</span>
                <i class="el-icon-caret-bottom"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="logout"></el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </div>
      </div>

    </el-header>
    <el-container>
      <!--  -->
      <el-aside width="200px">
        <!--  -->
        <el-menu background-color="#fafafa" active-text-color="#637ce9" :default-active="activePath">
          <el-menu-item index="patientLayout" @click="menuClick('patientLayout')">
            <i class="el-icon-s-home" style="font-size: 18px; "> </i>
          </el-menu-item>
          <el-menu-item index="oderOperate" @click="menuClick('orderOperate')">
            <i class="el-icon-monitor" style="font-size: 18px; "> </i>
          </el-menu-item>
          <el-menu-item index="myOrder" @click="menuClick('myOrder')">
            <i class="el-icon-postcard" style="font-size: 18px; "> </i>
          </el-menu-item>
          <el-menu-item index="myBed" @click="menuClick('myBed')">
            <i class="el-icon-news" style="font-size: 18px; "> </i>
          </el-menu-item>
          <el-menu-item index="patientCard" @click="menuClick('patientCard')"><i class="el-icon-user"
              style="font-size: 18px; "> </i>
          </el-menu-item>
          <el-menu-item index="ChatBot" @click="menuClick('ChatBot')"><i class="el-icon-s-opportunity" style="font-size: 18px; ">
              AI</i>
          </el-menu-item>
        </el-menu>
      </el-aside>
      <el-main>
        <!--  -->
        <router-view :key="componentKey"></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>
<script>
import request from "@/utils/request"; //  request 
import jwtDecode from "jwt-decode";
import {
  getToken,
  clearToken,
  getActivePath,
  setActivePath,
} from "@/utils/storage.js";
export default {
  name: "Patient",
  data() {
    return {
      userName: "",
      activePath: "",
      currentTime: this.getFormattedTime(),
      componentKey: 0, //  key 
    };
  },
  // 
  mounted() {
    this.getUserInfo();
    this.timer = setInterval(this.updateTime, 1000); // 
  },
  methods: {
    getUserInfo() {
      const token = getToken(); //  token
      if (!token) {
        return this.$message.error("");
      }

      request.get("/getUserInfo", {
        headers: { Authorization: `Bearer ${token}` } //  token
      })
        .then(res => {
          if (res.data.status !== 200) {
            return this.$message.error(",");
          }
          this.userName = res.data.data.userName; // 
        })
        .catch(err => {
          console.error(err);
          this.$message.error("");
        });
    },
    handleCommand(command) {
      if (command === "logout") {
        this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning",
        })
          .then(() => {
            clearToken();
            this.$message({
              type: "success",
              message: "!",
            });
            this.$router.push("login");
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "",
            });
          });

      }
    },
    //token
    tokenDecode(token) {
      if (token !== null) return jwtDecode(token);
    },
    //
    menuClick(path) {
      this.activePath = path;
      setActivePath(path);
      if (this.$route.path !== "/" + path) this.$router.push(path);
      console.log(path);
    },
    //
    logout() {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          clearToken();
          this.$message({
            type: "success",
            message: "!",
          });
          this.$router.push("login");
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    getFormattedTime() {
      const now = new Date();
      const year = now.getFullYear();
      const month = now.getMonth() + 1; //  0  +1
      const day = now.getDate();
      const hours = String(now.getHours()).padStart(2, "0");
      const minutes = String(now.getMinutes()).padStart(2, "0");
      const seconds = String(now.getSeconds()).padStart(2, "0");

      return `${year}${month}${day} ${hours}:${minutes}:${seconds}`;
    },
    updateTime() {
      this.currentTime = this.getFormattedTime();
    },
    reloadComponent() {
      this.componentKey++; //  key 
      return this.$message.success("");
    },
  },
  beforeUnmount() {
    clearInterval(this.timer); // 
  },
  created() {
    //  
    this.activePath = getActivePath();
    // token
    this.userName = this.tokenDecode(getToken()).pName;
  },
};
</script>
