<template>
    <div>
        <el-card>
            <!--  -->
            <el-row type="flex">
                <el-col :span="6">
                    <el-input v-model="query" placeholder="id">
                        <el-button slot="append" icon="el-icon-search" @click="requestOrders"></el-button>
                    </el-input>
                </el-col>
            </el-row>
            <el-table :data="orderData" stripe style="width: 100%" border>
                <el-table-column prop="oId" label="" width="80px"></el-table-column>
                <el-table-column prop="dId" label="id" width="80px"></el-table-column>

                <el-table-column prop="pId" label="id" width="100px">
                </el-table-column>

                <el-table-column prop="oStart" label="" width="190px"></el-table-column>
                <el-table-column prop="oEnd" label="" width="180px"></el-table-column>
                <el-table-column prop="oRecord" label="" width="200px"></el-table-column>
                <el-table-column prop="oDrug" label="" width="180px"></el-table-column>
                <el-table-column prop="oCheck" label="" width="180px"></el-table-column>
                <el-table-column prop="TotalPrice" label="/" width="80px"></el-table-column>
                <el-table-column prop="oPriceState" label="" width="100px">
                    <template slot-scope="scope">
                        <el-tag type="success" v-if="scope.row.oPriceState === 1"></el-tag>
                        <el-tag type="danger" v-if="
                            scope.row.oPriceState === 0 &&
                            scope.row.oState === 1
                        "></el-tag>
                    </template>
                </el-table-column>
                <el-table-column prop="oState" label="" width="100px">
                    <template slot-scope="scope">
                        <el-tag type="success" v-if="
                            scope.row.oState === 1 &&
                            scope.row.oPriceState === 1
                        "></el-tag>
                        <el-tag type="danger" v-if="
                            scope.row.oState === 0 && scope.row.oState === 0
                        "></el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="" width="140" fixed="right">
                    <template slot-scope="scope">
                        <el-button type="warning" icon="iconfont icon-r-paper" style="font-size: 14px"
                            @click="dealClick(scope.row.oId, scope.row.pId)" v-if="
                                scope.row.oState === 1 &&
                                scope.row.oPriceState === 1
                            "></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!--  -->
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" background
                layout="total, sizes, prev, pager, next, jumper" :current-page="pageNumber" :page-size="size"
                :page-sizes="[1, 2, 4, 8, 16]" :total="total">
            </el-pagination>
        </el-card>
    </div>
</template>
<script>
import request from "@/utils/request.js";
import jwtDecode from "jwt-decode";
import { getToken } from "@/utils/storage.js";
export default {
    name: "DoctorOrder",
    data() {
        return {
            userId: 1,
            orderData: [],
            pageNumber: 1,
            size: 8,
            query: "",
            total: 3,
        };
    },
    methods: {
        //
        dealClick(oId, pId) {
            this.$router.push({
                path: "/dealOrderAgain",
                query: {
                    oId: oId,
                    pId: pId,
                },
            });
        },
        //
        handleSizeChange(size) {
            console.log(size);
            this.size = size;
            this.requestOrders();
        },
        //   
        handleCurrentChange(num) {
            console.log(num);
            this.pageNumber = num;
            this.requestOrders();
        },

        //
        requestOrders() {
            request
                .get("doctor/findOrderByDid", {
                    params: {
                        dId: this.userId,
                        pageNumber: this.pageNumber,
                        size: this.size,
                        query: this.query,
                    },
                })
                .then((res) => {
                    if (res.data.status !== 200)
                        this.$message.error("");
                    console.log(res.data);

                    this.orderData = res.data.data;
                    this.total = res.data.total;
                });
        },
        //token
        tokenDecode(token) {
            if (token !== null) return jwtDecode(token);
        },
    },
    created() {
        const token = getToken(); //  token
        this.userId = jwtDecode(token).user_id;
        this.requestOrders();
    },
};
</script>
<style lang="scss" scoped>
.el-table {
    margin-top: 20px;
    margin-bottom: 20px;
}
</style>