<template>
  <div style="height: 100vh;
  display: flex; align-items: center;
  justify-content: center;
 background-color: #427cb3;;
" class="login-wrap">
    <div
      style="display: flex; background-color: rgba(255, 255, 255, 0.82); width: 50%; border-radius: 5px; overflow: hidden">
      <div style="flex: 1">
        <img src="@/assets/login1.png" alt="" style="width: 400px;height: 400px" />
      </div>
      <div style="flex: 1; display: flex; align-items: center; justify-content: center;">
        <el-form :model="loginForm" style="width: 80%" :rules="loginRules" ref="ruleForm">
          <div style="font-size: 20px; font-weight: bold; margin-bottom: 20px;">
            <i></i>
          </div>
          <div style="font-size: small;color:darkgray"></div>
          <el-form-item prop="id">
            <!--v-model---->
            <el-input v-model="loginForm.id">
              <i slot="prefix" class="el-input__icon el-icon-user"></i>
            </el-input>
          </el-form-item>

          <el-form-item prop="password">
            <el-input v-model="loginForm.password" size="medium" placeholder="" clearable show-password>
              <i slot="prefix" class="el-input__icon el-icon-lock"></i>
            </el-input>
          </el-form-item>
          <el-form-item class="role">
            <el-radio-group v-model="role" size="small">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" style="width: 100%" @click="submitLoginForm('ruleForm')"> </el-button>
          </el-form-item>

          <div style="display: flex">
            <div style="flex: 1">
              
              <span style="color: #0f9876; cursor: pointer" @click="registerFormVisible = true"></span>
            </div>

          </div>
        </el-form>
      </div>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="registerFormVisible">
      <el-form class="findPassword" :model="registerForm" :rules="registerRules" ref="registerForm">
        <el-form-item label="" label-width="80px" prop="pId">
          <el-input v-model.number="registerForm.pId"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="80px">
          <el-radio v-model="registerForm.pGender" label=""></el-radio>
          <el-radio v-model="registerForm.pGender" label=""></el-radio>
        </el-form-item>
        <el-form-item label="" label-width="80px" prop="pPassword">
          <el-input v-model="registerForm.pPassword"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="80px" prop="pName">
          <el-input v-model="registerForm.pName"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="80px" prop="pBirthday">
          <el-date-picker v-model="registerForm.pBirthday" type="date" placeholder="" value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" label-width="80px" prop="pPhone">
          <el-input v-model="registerForm.pPhone" maxlength="11"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="80px" prop="pEmail">
          <el-input v-model="registerForm.pEmail"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="80px" prop="pCard">
          <el-input v-model="registerForm.pCard" maxlength="18"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="registerFormVisible = false"> </el-button>
        <el-button type="primary" @click="registerClick('registerForm')"> </el-button>
      </div>
    </el-dialog>
  </div>

</template>

<script>
import request from "@/utils/request.js";
import { setToken } from "@/utils/storage.js";
export default {
  name: "Login",
  data() {
    var validateMoblie = (rule, value, callback) => {
      if (value === undefined) {
        callback(new Error(""));
      } else {
        let reg = /^1(3[0-9]|4[5,7]|5[0,1,2,3,5,6,7,8,9]|6[2,5,6,7]|7[0,1,7,8]|8[0-9]|9[1,3,8,9])\d{8}$/;
        if (!reg.test(value)) {
          callback(new Error(""));
        }
        callback();
      }
    };
    var validateCard = (rule, value, callback) => {
      if (value === undefined) {
        callback(new Error(""));
      } else {
        let reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
        if (!reg.test(value)) {
          callback(new Error(""));
        }
        callback();
      }
    };
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        if (this.findForm.checkPassword !== "") {
          this.$refs.findForm.validateField("checkPassword");
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.findForm.newPassword) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    return {
      //
      backgroundDiv: {
        backgroundImage: "url(" + require("../assets/doctor.jpg") + ")",
        backgroundRepeat: "no-repeat",
        backgroundSize: "100% 100%"
      },
      loginForm: {
        id: "202301",
        password: "123456"
      },
      loginRules: {
        id: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 50, message: " 3 50 ", trigger: "blur" }
        ],
        password: [{ required: true, message: "", trigger: "blur" }]
      },
      role: "",
      findRole: "",
      //
      findFormVisible: false,
      findForm: {
        code: "",
        newPassword: "",
        checkPassword: "",
        pEmail: ""
      },

      findRules: {
        pEmail: [
          { required: true, message: "", trigger: "blur" },
          {
            type: "email",
            message: "",
            trigger: ["blur", "change"]
          }
        ],
        code: [{ required: true, message: "", trigger: "blur" }],
        newPassword: [{ validator: validatePass, trigger: "blur" }],
        checkPassword: [{ validator: validatePass2, trigger: "blur" }]
      },
      totalTime: 60,
      content: "",
      canClick: true,
      //
      registerFormVisible: false,
      registerForm: {
        pGender: ""
      },
      registerRules: {
        pId: [
          { required: true, message: "", trigger: "blur" },
          { type: "number", message: "", trigger: "blur" }
        ],
        pPassword: [
          { required: true, message: "", trigger: "blur" },
          { min: 4, max: 50, message: " 4 50 ", trigger: "blur" }
        ],
        pName: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 8, message: " 2 8 ", trigger: "blur" }
        ],
        pEmail: [
          { required: true, message: "", trigger: "blur" },
          {
            type: "email",
            message: "",
            trigger: ["blur", "change"]
          }
        ],
        pPhone: [{ validator: validateMoblie }],
        pCard: [{ validator: validateCard }],
        pBirthday: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    //
    registerClick(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          request
            .post("/patient/addPatient", {
              pId: this.registerForm.pId,
              pName: this.registerForm.pName,
              pPassword: this.registerForm.pPassword,
              pGender: this.registerForm.pGender,
              pEmail: this.registerForm.pEmail,
              pPhone: this.registerForm.pPhone,
              pCard: this.registerForm.pCard,
              pBirthday: this.registerForm.pBirthday
            })
            .then(res => {
              if (res.data.status !== 200)
                return this.$message.error(res.data.message);
              this.registerFormVisible = false;
              this.$message.success(res.data.message);
            });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    // 
    findPassword(findForm) {
      this.$refs[findForm].validate(valid => {
        if (valid) {
          //
          if (this.findRole === "") {
            request
              .get("patient/findPassword", {
                params: {
                  pEmail: this.findForm.pEmail,
                  pPassword: this.findForm.newPassword,
                  code: this.findForm.code
                }
              })
              .then(res => {
                if (res.data.status !== 200)
                  return this.$message.error("");
                this.$message.success("");
                this.findFormVisible = false;
              });
          }
          //
          if (this.findRole === "") {
            request
              .get("admin/findPassword", {
                params: {
                  aEmail: this.findForm.pEmail,
                  aPassword: this.findForm.newPassword,
                  code: this.findForm.code
                }
              })
              .then(res => {
                if (res.data.status !== 200)
                  return this.$message.error("");
                this.$message.success("");
                this.findFormVisible = false;
              });
          }
          //
          if (this.findRole === "") {
            request
              .get("doctor/findPassword", {
                params: {
                  dEmail: this.findForm.pEmail,
                  dPassword: this.findForm.newPassword,
                  code: this.findForm.code
                }
              })
              .then(res => {
                if (res.data.status !== 200)
                  return this.$message.error("");
                this.$message.success("");
                this.findFormVisible = false;
              });
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    //
    sendEmail() {
      //
      if (!this.canClick) return; //
      this.canClick = false;
      this.content = this.totalTime + "s";
      let clock = window.setInterval(() => {
        this.totalTime--;
        this.content = this.totalTime + "s";
        if (this.totalTime < 0) {
          window.clearInterval(clock);
          this.content = "";
          this.totalTime = 10;
          this.canClick = true; //
        }
      }, 1000);

      //
      if (this.findRole === "") {
        request
          .get("patient/sendEmail", {
            params: {
              pEmail: this.findForm.pEmail
            }
          })
          .then(res => {
            console.log(this.findForm.pEmail);
            console.log(res);
            if (res.data.status !== 200)
              return this.$message.error("");
            this.$message.success("");
          });
      }
      //
      if (this.findRole === "") {
        request
          .get("admin/sendEmail", {
            params: {
              aEmail: this.findForm.pEmail
            }
          })
          .then(res => {
            console.log(this.findForm.pEmail);
            console.log(res);
            if (res.data.status !== 200)
              return this.$message.error("");
            this.$message.success("");
          });
      }
      //
      if (this.findRole === "") {
        request
          .get("doctor/sendEmail", {
            params: {
              dEmail: this.findForm.pEmail
            }
          })
          .then(res => {
            console.log(this.findForm.pEmail);
            console.log(res);
            if (res.data.status !== 200)
              return this.$message.error("");
            this.$message.success("");
          });
      }
    },
    // 
    submitLoginForm(formName) {
      this.$refs[formName].validate(valid => {
        if (!valid) {
          console.log("error submit!!");
          return false;
        }

        const roleMap = {
          "": { idKey: "aId", passwordKey: "aPassword", url: "admin/login", redirect: "/adminLayout" },
          "": { idKey: "dId", passwordKey: "dPassword", url: "doctor/login", redirect: "/doctorLayout" },
          "": { idKey: "pId", passwordKey: "pPassword", url: "patient/login", redirect: "/patientLayout" }
        };

        const roleConfig = roleMap[this.role];
        if (!roleConfig) {
          this.$message.error("");
          return;
        }

        let params = new URLSearchParams();
        params.append(roleConfig.idKey, this.loginForm.id);
        params.append(roleConfig.passwordKey, this.loginForm.password);
        params.append("user_role", this.role);
        console.log(this.role);


        request.post(roleConfig.url, params)
          .then(res => {
            console.log(res);
            if (res.data.status !== 200) {
              return this.$message.error("");
            }
            setToken(res.data.data.token);
            this.$router.push(roleConfig.redirect);
          })
          .catch(err => {
            this.$message.error("");
            console.error(err);
          });
      });
    }
  }
};
</script>

<style lang="scss">
.login-wrap {
  position: relative;
  width: 100%;
  height: 100%;
  background: #0f9876;
  background-image: url("../assets/doctor.jpeg");
  background-size: 100% 100%;
}
</style>
