<template>
    <el-card>
        <el-table :data="orderData" stripe style="width: 100%" border>
            <el-table-column label="" type="index" width="50">
            </el-table-column>
            <el-table-column label="" prop="oId"></el-table-column>
            <el-table-column label="id" prop="pId"></el-table-column>
            <el-table-column label="" prop="pName"></el-table-column>
            <el-table-column label="" prop="dName"></el-table-column>
            <el-table-column label="" prop="oStart" width="200px"></el-table-column>
            <el-table-column label="" prop="oGhAlipay" width="200px">
                <template slot-scope="scope">
                    <el-tag type="success" v-if="
                        scope.row.oGhAlipay === 'PAID'
                    "></el-tag>
                    <el-tag type="danger" v-if="
                        scope.row.oGhAlipay === null
                    "></el-tag>
                </template>
            </el-table-column>
            <el-table-column label="">
                <template slot-scope="scope">
                    <el-button type="warning" style="font-size: 18px" @click="dealClick(scope.row.oId, scope.row.pId)"
                        v-if="
                            scope.row.oGhAlipay === 'PAID'
                        ">
                        <i class="el-icon-monitor" style="font-size: 18px"></i>
                        
                    </el-button>
                </template>
            </el-table-column>

        </el-table>
    </el-card>
</template>
<script>
import jwtDecode from "jwt-decode";
import { getToken } from "@/utils/storage.js";
import request from "@/utils/request.js";
export default {
    name: "orderToday",
    data() {
        return {
            userId: 1,
            userName: "dada",
            today: "",

            orderData: [],

        }
    },
    methods: {
        ////
        dealClick(oId, pId) {
            this.$router.push(
                {
                    path: "/dealOrder",
                    query: {
                        oId: oId,
                        pId: pId
                    }
                }
            );

        },
        //
        requestOrder() {
            request.get("doctor/findOrderByToday", {
                params: {
                    dId: this.userId,
                    oStart: this.today
                }
            })
                .then(res => {
                    if (res.data.status != 200)
                        return this.$message.error("");
                    this.orderData = res.data.data;
                    console.log(res.data.data);
                })
        },
        //token
        tokenDecode(token) {
            if (token !== null)
                return jwtDecode(token);
        },
        //
        nowDay() {
            const nowDate = new Date();
            let date = {
                year: nowDate.getFullYear(),
                month: nowDate.getMonth() + 1,
                date: nowDate.getDate(),
            };
            if (date.date < 10) {
                date.date = "0" + date.date
            }
            if (date.month < 10) {
                date.month = "0" + date.month
            }
            this.today = date.year + "-" + date.month + "-" + date.date;

        },
    },
    created() {
        const token = getToken(); //  token
        this.userId = jwtDecode(token).user_id;
        console.log(this.userId);
        //
        this.nowDay();
        //
        this.requestOrder();

    },
}
</script>