<template>
  <div>

    <div class="indexPeople" style="margin-left: 10%">

      <div class="userImage">
        <i class="el-icon-user" style="font-size: 132px"></i>
      </div>

      <div class="userFont">
        <div class="spanCure">
          <span></span>
        </div>
        <div class="spanPeople">
          <span>{{ orderPeople }}</span>
        </div>
      </div>
    </div>
    <!--  -->
    <el-card>
      <el-row>
        <el-table :data="arranges" stripe style="width: 100%;font-size: larger;" border>
          <el-table-column prop="ar_time" label=""></el-table-column>
        </el-table>
      </el-row>
    </el-card>

    <el-row>
      <el-col :span="24">
        <img src="@/assets/16.png" style="width: 641px;margin-left: 490px;">
      </el-col>
    </el-row>
  </div>
</template>
<script>
import request from "@/utils/request.js";
import jwtDecode from "jwt-decode";
import { getToken } from "@/utils/storage.js";
export default {
  name: "DoctorLayout",
  data() {
    return {
      userId: 1,
      orderPeople: 1,
      arranges: [], // 
    };
  },
  methods: {
    requestPeople() {
      request
        .get("doctor/orderPeopleByDid", {
          params: {
            dId: this.userId,
          },
        })

        .then((res) => {
          if (res.data.status !== 200)
            return this.$message.error("");
          this.orderPeople = res.data.data;
        });
    },
    // 
    requestSchedule() {
      request
        .get("doctor/arrangeByDid", {
          params: {
            dId: this.userId,
          },
        })
        .then((res) => {
          if (res.data.status === 200) {
            this.arranges = res.data.data;
          } else {
            this.$message.error("");
          }
        })
        .catch(() => {
          this.$message.error("");
        });
    },
  },
  created() {
    const token = getToken(); //  token
    this.userId = jwtDecode(token).user_id;
    console.log(this.userId);
    this.requestPeople();
    this.requestSchedule(); // 
  },
};
</script>
